<template>
  <div>
    <van-nav-bar title="编辑资料" left-arrow @click-left="$router.back()" />

    <!-- 修改内容 -->
    <div class="edit-content">
      <van-cell title="头像" is-link center>
        <template>
          <img class="avatar" :src="userIntro.photo" alt="" @click="$refs.iptFile.click()" />
        </template>
      </van-cell>
      <!-- file选择器 -->
      <input type="file" ref="iptFile" v-show="false" accept="image/*" @change="onFileChange" />
      <van-cell title="名称" :value="userIntro.name" is-link center @click="showNameDialog"> </van-cell>
      <van-cell title="性别" :value="gender" is-link center @click="showGenderPicker = true"> </van-cell>
      <van-cell title="生日" :value="userIntro.birthday" is-link center @click="showDatePickerFn"> </van-cell>
    </div>

    <!-- 修改名称的对话框 -->
    <van-dialog v-model="isNameDialogShow" title="修改名称" show-cancel-button :before-close="NameDialogBeforeClose">
      <van-field
        ref="field"
        v-model.trim="username"
        maxlength="12"
        placeholder="请输入名称"
        input-align="center"
        v-focus
      />
    </van-dialog>

    <!-- 性别选择器 -->
    <van-popup v-model="showGenderPicker" round position="bottom" style="height: 30%" get-container="body">
      <van-picker
        title="性别"
        show-toolbar
        :columns="genders"
        visible-item-count="3"
        @confirm="genderConfirm"
        @cancel="showGenderPicker = false"
      />
    </van-popup>

    <!-- 出生日期选择器 -->
    <van-popup v-model="showDatePicker" round position="bottom" get-container="body">
      <van-datetime-picker
        v-model="birthday"
        type="date"
        title="选择出生日期"
        :min-date="minDate"
        :max-date="maxDate"
        @cancel="showDatePicker = false"
        @confirm="birthdayConfirm"
      />
    </van-popup>
  </div>
</template>

<script>
import { Notify } from 'vant'
import { formatDate } from '@/utils/date'
export default {
  name: 'UserEditor',
  data() {
    return {
      userIntro: {},
      username: '', // 收集用户修改的姓名
      isNameDialogShow: false, // 显示或隐藏修改姓名的对话框
      genders: ['男', '女'],
      showGenderPicker: false, // 显示隐藏性别选择器
      showDatePicker: false, // 显示隐藏出生日期选择器
      birthday: '', // 收集用户修改的生日
      minDate: new Date('1920-01-01'),
      maxDate: new Date()
    }
  },
  created() {
    this.getUserIntro()
  },
  methods: {
    async getUserIntro() {
      const res = await this.$API.reqUserIntro()
      this.userIntro = res.data.data
    },
    // 显示修改用户名的对话框
    showNameDialog() {
      this.username = this.userIntro.name
      this.isNameDialogShow = true
    },
    // 用户名修改对话框关闭前的回调
    async NameDialogBeforeClose(action, done) {
      if (action === 'cancel' || this.username === this.userIntro.name) return done()
      // 点击确认
      // \u4e00-\u9fa5 指汉字
      if (/^[a-zA-Z0-9\u4e00-\u9fa5]{1,12}$/.test(this.username)) {
        done()
        await this.$API.reqEdit({ name: this.username })
        // 更新显示
        this.userIntro.name = this.username
      } else {
        Notify({ type: 'danger', message: '用户名必须为1-12位字母、数字或汉字组成' })
        done(false)
      }
    },
    // 性别选择器确认按钮回调
    async genderConfirm(value) {
      // value 为选择值
      const gender = value === '男' ? 0 : 1
      await this.$API.reqEdit({ gender })
      this.showGenderPicker = false
      // 更新显示
      this.userIntro.gender = gender
    },
    // 显示生日选择器
    showDatePickerFn() {
      // 将日期字符串转换成日期对象传进去
      this.birthday = new Date(this.userIntro.birthday)
      this.showDatePicker = true
    },
    // 生日选择器确认按钮的回调
    async birthdayConfirm(value) {
      // 将日期对象转换成日期字符串
      const birthday = formatDate(value)
      this.showDatePicker = false
      await this.$API.reqEdit({ birthday })
      this.userIntro.birthday = birthday
    },
    // 头像修改
    async onFileChange(e) {
      if (e.target.files.length === 0) return
      // 创建FormData实例
      const fd = new FormData()
      fd.append('photo', e.target.files[0])
      const res = await this.$API.reqUpdateAvatar(fd)
      this.userIntro.photo = res.data.data.photo
      // 存到vuex, 因为组件缓存, 修改头像后返回user界面头像不会更新,
      // 所以user那里要使用vuex获取头像
      this.$store.commit('GET_AVATAR', res.data.data.photo)
    }
  },
  computed: {
    // 性别
    gender() {
      return this.userIntro.gender ? '女' : '男'
    }
  }
}
</script>

<style lang="less" scoped>
.avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-color: #f8f8f8;
}
</style>
